<template>
    <header class="navigation d-flex flex-wrap justify-content-center py-3 border-bottom">
      <a href="/" class="d-flex align-items-center mb-3 mb-md-0 me-md-auto link-body-emphasis text-decoration-none">
        <span class="navigation-title green-text fs-4">EcoTime</span>
      </a>

      <ul class="nav nav-pills">
        <router-link class="router-link" to="/"><li class="nav-item"><a href="#" @click="navigationItemSwap('0')" class="nav-link">Home</a></li></router-link>
        <router-link class="router-link" to="/about-us"><li class="nav-item"><a href="#" @click="navigationItemSwap('1')" class="nav-link">About us</a></li></router-link>
        <router-link class="router-link" to="/blog"><li class="nav-item"><a href="#" @click="navigationItemSwap('2')" class="nav-link">Blog</a></li></router-link>
      </ul>
    </header>
</template>

<script>
export default {
  mounted() {
    var location = this.$route.path
    var locationEnum = {
      "/": 0,
      "/about-us": 1,
      "/blog": 2
    }
    document.getElementsByClassName("nav-item")[locationEnum[location]].getElementsByClassName("nav-link")[0].classList.add("active")
  },
  methods: {
    navigationItemSwap(value) {
        document.getElementsByClassName("active")[0].classList.remove("active")
        document.getElementsByClassName("nav-item")[value].getElementsByClassName("nav-link")[0].classList.add("active")
    }
  },
}
</script>

<style scoped>
.navigation {
    position: relative;
    z-index: 2;
    background-color: #2c3e50cc;
    text-align: center;
}
.navigation-title {
  padding: 0 20px;
}
.green-text {
    color: #4cb34c;
}
.nav-link {
    color: white;
}
.nav-link:hover {
    color: rgb(212, 212, 212);
}
.nav-item > .active {
    background-color: #4cb34c;
}
.nav-pills {
    margin-right: 20px;
}
</style>


