<template>
  <div class="d-flex text-center main">
    <div class="image-section">
      <div class="cover-image"></div>
    </div>
    <div class="header-content">
      <h1 class="header-content-title">EcoTime</h1>
      <h3 class="header-content-subtitle">Your daily dose of eco life</h3>
      <div class="header-content-button" @click="scrollToPhotoSection()">&#8680;</div>
    </div>
    <div class="photo-content-section">
      <div class="section-title">Recent articles</div>
      <div class="photo-content-track" id="image-track" data-mouse-down-at="0" data-prev-percentage="0">
        <img class="photo-content-image" src="../assets/butterfly.jpg" draggable="false">
        <img class="photo-content-image" src="../assets/flower_field.jpg" draggable="false">
        <img class="photo-content-image" src="../assets/forest.jpg" draggable="false">
        <img class="photo-content-image" src="../assets/tsunami.jpg" draggable="false">
        <img class="photo-content-image" src="../assets/volcano.jpg" draggable="false">
        <img class="photo-content-image" src="../assets/wind_turbines.png" draggable="false">
        <img class="photo-content-image" src="../assets/tornado.jpg" draggable="false">
      </div>
    </div>
    <div class="info-section">
      <div class="section-title">Information</div>
      <div class="info-row">
        <div class="info-section-image"><img src="../assets/tornado.jpg"></div>
        <div class="info-section-text">
          <h2 class="info-section-text-title">Something</h2>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas convallis sem a nisl dictum tincidunt. Ut ut cursus justo, eu luctus tortor. Duis a magna non mauris tempor aliquet id et diam. Nulla id molestie felis. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse magna magna, ultricies mattis elit eu, consequat tincidunt lectus. Curabitur commodo, nibh at consectetur lobortis, felis dolor blandit lorem, ac pharetra elit justo eu elit. Nunc vitae volutpat quam. Sed porttitor erat eget fringilla mattis. Vivamus pulvinar nulla at quam sagittis tristique quis iaculis ex.</p>
        </div>
      </div>
      <div class="info-row">
        <div class="info-section-image"><img src="../assets/tsunami.jpg"></div>
        <div class="info-section-text">
          <h2 class="info-section-text-title">Something else</h2>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas convallis sem a nisl dictum tincidunt. Ut ut cursus justo, eu luctus tortor. Duis a magna non mauris tempor aliquet id et diam. Nulla id molestie felis. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse magna magna, ultricies mattis elit eu, consequat tincidunt lectus. Curabitur commodo, nibh at consectetur lobortis, felis dolor blandit lorem, ac pharetra elit justo eu elit. Nunc vitae volutpat quam. Sed porttitor erat eget fringilla mattis. Vivamus pulvinar nulla at quam sagittis tristique quis iaculis ex.</p>
        </div>
      </div>
      <div class="info-row">
        <div class="info-section-image"><img src="../assets/volcano.jpg"></div>
        <div class="info-section-text">
          <h2 class="info-section-text-title">Something else entirely</h2>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas convallis sem a nisl dictum tincidunt. Ut ut cursus justo, eu luctus tortor. Duis a magna non mauris tempor aliquet id et diam. Nulla id molestie felis. Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse magna magna, ultricies mattis elit eu, consequat tincidunt lectus. Curabitur commodo, nibh at consectetur lobortis, felis dolor blandit lorem, ac pharetra elit justo eu elit. Nunc vitae volutpat quam. Sed porttitor erat eget fringilla mattis. Vivamus pulvinar nulla at quam sagittis tristique quis iaculis ex.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    scrollToPhotoSection() {
      document.getElementsByClassName('photo-content-section')[0].scrollIntoView({behavior: 'smooth'})
    }
  },
  mounted() {
    const track = document.getElementById("image-track");
    window.onmousedown = e => {
      track.dataset.mouseDownAt = e.clientX;
    }
    window.onmousemove = e => {
      if(track.dataset.mouseDownAt === "0") return;
      const mouseDelta = parseFloat(track.dataset.mouseDownAt) - e.clientX,
            maxDelta = window.innerWidth / 2;

      const percentage = (mouseDelta / maxDelta) * -100,
            nextPercentage = parseFloat(track.dataset.prevPercentage) + percentage;
      
      track.dataset.percentage = nextPercentage;
      track.style.transform = `translate(${nextPercentage}%, -50%)`;
    }
    window.onmouseup = () => {
      track.dataset.mouseDownAt = "0";
      track.dataset.prevPercentage = track.dataset.percentage;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.photo-content-section {
  position: relative;
  top: 80px;
  width: 75%;
  margin: 0 auto;
  overflow: hidden;
}
.photo-content-track {
  margin-top: 150px;
  display: flex;
  left: 50%;
  top: 50%;
  transform: translate(0, -50%);
  gap: 20px;
  cursor: pointer;
}
.photo-content-image {
  width: 400px;
  height: 240px;
  object-fit: cover;
  object-position: center;
  box-shadow: 4px 4px 40px black;
}

.info-section {
  margin: auto;
  margin-top: 150px;
  max-width: 1000px;
}
.info-row {
  display: flex;
  flex-direction: row;
  margin-top: 50px;
  gap: 40px;
}
.info-row:nth-of-type(2n+1) {
  flex-direction: row-reverse;
}
.info-section-image img {
  height: 300px;
  box-shadow: 4px 4px 20px black;
}
.info-section-text {
  position: relative;
  text-align: start;
  padding: 15px 20px;
  width: 600px;
  max-height: 290px;
  overflow: hidden;
}
.info-section-text-title {
  margin-bottom: 20px;
}

@media screen and (max-width: 1100px) {
  .info-row {
    gap: 20px;
  }
  .info-section-image, .info-section-text {
    margin: auto;
    width: 100%;
    height: auto;
  }
  .info-section-image img {
    width: 100%;
    height: auto;
  }

  .photo-content-section {
    width: 100%;
  }
  .photo-content-image {
    width: 320px;
    height: 192px;
  }
  .info-section {
    margin-top: 50px;
    padding: 10px;
  }
  .info-row {
    margin: 50px 0;
    flex-direction: column !important;
  }

  .info-section-text-title {
    font-size: 18px;
  }
  .info-section-text {
    font-size: 14px;
    padding: 0;
  }
}
</style>



