<template>
  <div id="app">
    <Navigation />
    <router-view/>
    <Footer />
  </div>
</template>

<script>
import Navigation from "./components/Navigation"
import Footer from "./components/Footer"
export default {
  name: 'App',
  components: {
    Navigation,
    Footer
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #2c3e50;
  min-height: 100vh;
  color: white;
}
.row {
  display: flex;
  flex-direction: row;
}
.main {
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
  min-height: calc(100vh - 348px);
}
.router-link {
  text-decoration: none;
}
.section-title {
  font-size: 58px;
}

.image-section {
  width: 100%;
  margin-top: -85px;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  overflow: hidden;
  opacity: 0.8;
}
.cover-image {
  background-image: url('./assets/nature.jpg');
  height: 100vh;
  margin: -15px;
  margin-bottom: 25px;
  transform: scale(1.1);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
}
.header-content {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  z-index:2;
  letter-spacing: 2px;
}
.header-content-title {
  font-size: 80px;
  padding: 20px 0;
}
.header-content-subtitle {
  font-size: 32px;
}
.header-content-button {
  font-size: 56px;
  color: white;
  background-color: #4cb34c;
  border: 2px solid white;
  width: 180px;
  border-radius: 12px;
  margin: 40px auto;
  cursor: pointer;
  transition: 0.2s;
}
.header-content-button:hover {
  background-color: #418d41;
}

@media screen and (max-width: 1100px) {
  .header-content-title {
    font-size: 48px;
    padding: 10px;
  }  
  .header-content-subtitle {
    font-size: 28px;
    padding: 10px;
  }
  .header-content-button {
    font-size: 40px;
    width: 120px;
    margin: 20px auto;
  }
  .section-title {
    font-size: 32px;
  }
}
</style>



